* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding-left: 100px;
  padding-right: 100px;
  padding-top: 100px;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.country-list-item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.country-list-img {
  display: block;
  width: 200px;
  margin-right: 15px;
}

.country-list-name {
  font-size: 25px;
}

.country-info-img {
  display: block;
  width: 200px;
  margin-right: 15px;
}

.country-info-name {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 25px;
}

.country-info-text {
  font-weight: 700;
}
